body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {  margin: 0;}.app {  background-color: #fafafa;}.app__posts {  padding: 20px;  display: flex;  justify-content: center;}.app__postsRight {  margin-left: 15px;}.app__header {  position: -webkit-sticky;  position: sticky;  top: 0;  z-index: 1;  background-color: #ffffff;  padding: 20px;  border-bottom: 1px solid lightgray;  object-fit: contain;  display: flex;  justify-content: space-between;}.app__headerImage {  object-fit: contain;}.app__signup {  display: flex;  flex-direction: column;}
.post {    max-width: 500px;    background-color: white;    border: 1px solid lightgray;    margin-bottom: 45px;}.post__image {    width: 100%;    object-fit: contain;    border-top: 1px solid lightgray;    border-bottom: 1px solid lightgray;}.post__text {    font-weight: normal;    padding: 20px;}.post__avatar {    margin-right: 10px;}.post__header {    display: flex;    align-items: center;    padding: 20px;}.post__commentBox {    display: flex;    margin-top: 10px;}.post__comments {    padding: 20px;}.post__input {    flex: 1 1;    border: none;    padding: 10px;    border-top: 1px solid lightgray;}.post__button {    flex: 0 1;    border: none;    color: #6082a3;    background-color: transparent;    border-top: 1px solid lightgray;}
.imageupload {    display: flex;    flex-direction: column;    width: 60%;    margin: 10px auto;    background-color: #ffffff;    border-top: 1px solid lightgray;}.imageupload__progress {    width: 100%;}
